import styleInject from '#style-inject';styleInject(".password-strength-meter {\n  width: 100%;\n  font-family:\n    -apple-system,\n    BlinkMacSystemFont,\n    \"Segoe UI\",\n    Roboto,\n    Oxygen,\n    Ubuntu,\n    Cantarell,\n    \"Open Sans\",\n    \"Helvetica Neue\",\n    sans-serif;\n}\n.password-strength-meter__progress {\n  position: relative;\n  width: 100%;\n  height: 4px;\n  background-color: #f3f4f6;\n  border-radius: 2px;\n  overflow: hidden;\n  margin-bottom: 8px;\n}\n.password-strength-meter__bar {\n  position: absolute;\n  top: 0;\n  left: 0;\n  height: 100%;\n  width: 0;\n  transition: width 300ms ease-out, background-color 300ms ease-out;\n}\n.password-strength-meter__label {\n  font-size: 14px;\n  font-weight: 500;\n  transition: color 300ms ease-out;\n  margin-top: 4px;\n  user-select: none;\n}\n.password-strength-meter__progress:hover {\n  background-color: #e5e7eb;\n}\n@media (prefers-reduced-motion: reduce) {\n  .password-strength-meter__bar,\n  .password-strength-meter__label {\n    transition: none;\n  }\n}\n@media (prefers-color-scheme: dark) {\n  .password-strength-meter__progress {\n    background-color: #374151;\n  }\n  .password-strength-meter__progress:hover {\n    background-color: #4b5563;\n  }\n}\n")